/* error */

.error_message {
  padding: 8px;
  background-color: var(--danger-hover);
  color: white;
  border-radius: 8px;
}

.error_message:hover {
  background-color: var(--danger);
}

/* form */

.form {
  display: grid;
  grid-template-rows: min-content auto;
}

.form input,
.form textarea {
  outline: 0;
  outline-offset: 0;
}

/* buttons */

.buttons_block {
  padding: 8px 0;
}

.buttons_block button {
  border: 2px solid var(--site-color);
  background-color: var(--site-color);
  color: white;
  padding: 8px 32px;
  border-radius: 8px;
  margin-right: 8px;
}

.buttons_block button:hover {
  background-color: transparent;
  color: var(--site-color);
}

/* title */

.title_block {
  padding: 8px 0;
}

.title_block input {
  width: calc(100% - 2px * 2 - 16px * 2);
  border: 2px solid lightgray;
  border-radius: 8px;
  padding: 16px;
  font-size: 1.6em;
}

/* date */

.date_block {
  padding: 8px 0;
}

.date_block input {
  border: 2px solid lightgray;
  border-radius: 8px;
  padding: 16px;
}

/* completed */

.check_block {
  display: flex;
  align-items: center;
  padding: 8px 0;
}

.check_block input {
  display: none;
}

.check_block label {
  width: 64px;
  height: 32px;
  border-radius: 32px;
  margin: 8px 8px 8px 0;
  background-color: var(--site-color-hover);
  display: block;
  border: 2px solid var(--site-color-hover) !important;
  display: flex;
  justify-content: left;
}

.check_block .checked {
  background-color: var(--site-color) !important;
  justify-content: right;
}

.check_block label::before {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: white;
  display: block;
  content: '';
}

/* description */

.description_block {
  padding: 8px 0;
}

.description_block > div {
  height: 100%;
}

.description_block textarea {
  width: calc(100% - 2px * 2 - 16px * 2) !important;
  height: calc(100% - 2px * 2 - 16px * 2 - 16px);
  border: 2px solid lightgray;
  border-radius: 8px;
  margin: 0 0 16px 0;
  padding: 16px;
}
